    border: 1px solid #0085A1; }
  .pager .disabled > a,
  .pager .disabled > a:hover,
  .pager .disabled > a:focus,
  .pager .disabled > span {
    color: gray;
    background-color: #404040;
    cursor: not-allowed; }

::-moz-selection {
  color: white;
  text-shadow: none;
  background: #0085A1; }

::selection {
  color: white;
  text-shadow: none;
  background: #0085A1; }

img::selection {
  color: white;
  background: transparent; }

img::-moz-selection {
  color: white;
  background: transparent; }

body {
  webkit-tap-highlight-color: #0085A1; }
